@charset "UTF-8";
:root {
  --clr-primary: #eb2f64;
  --clr-primary-light: #ff3366;
  --clr-primary-dark: #ba265d;
  --clr-white: #fff;
  --clr-grey-light-1: #faf9f9;
  --clr-grey-light-2: #f4f2f2;
  --clr-grey-light-3: #f0eeee;
  --clr-grey-light-4: #ccc;
  --clr-grey-dark-1: #333;
  --clr-grey-dark-2: #777;
  --clr-grey-dark-3: #999;
  --clr-black: #000;
  --shadow-dark: 0 2rem 6rem rgba(0, 0, 0, 0.6);
  --shadow-light: 0 2rem 5rem rgba(0, 0, 0, 0.3);
  --line: 1px solid var(--clr-grey-light-2);
}

* {
  padding: 0;
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}
@media only screen and (max-width: 1100px) {
  html {
    font-size: 50%;
  }
}

body {
  font-family: "Open sans", sans-serif;
  font-weight: 400;
  line-height: 1.6;
  color: var(--clr-grey-dark-2);
  background-image: linear-gradient(to right bottom, var(--clr-primary-light), var(--clr-primary-dark));
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
}

.logo {
  height: 3.25rem;
  margin-left: 2rem;
}

.search {
  flex: 0 0 40%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 500px) {
  .search {
    order: 1;
    flex: 0 0 100%;
  }
}
.search__input {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: var(--clr-grey-light-2);
  border: none;
  padding: 0.7rem 2rem;
  border-radius: 100px;
  width: 90%;
  margin-right: -3.5rem;
  transition: all 0.2s;
}
.search__input:focus {
  outline: none;
  width: 100%;
  background-color: var(--clr-grey-light-3);
}
.search__input::placeholder {
  font-weight: 100;
  color: var(--clr-grey-light-4);
}
.search__input:focus + .search__button {
  background-color: var(--clr-grey-light-3);
}
.search__button {
  border: none;
  background-color: var(--clr-grey-light-2);
}
.search__button:focus {
  outline: none;
}
.search__button:active {
  transform: translateY(2px);
}
.search__icon {
  height: 2rem;
  width: 2rem;
  fill: var(--clr-grey-dark-3);
}

.user-nav {
  margin-right: 2rem;
  align-self: stretch;
  display: flex;
  align-items: center;
}
.user-nav > * {
  padding: 0 2rem;
  cursor: pointer;
  height: 100%;
  display: flex;
  align-items: center;
}
.user-nav > *:hover {
  background-color: var(--clr-grey-light-2);
}
.user-nav__icon-box {
  position: relative;
}
.user-nav__icon {
  height: 2.5rem;
  width: 2.5rem;
  fill: var(--clr-grey-dark-2);
}
.user-nav__notification {
  font-size: 0.8rem;
  height: 1.75rem;
  width: 1.75rem;
  border-radius: 50%;
  background-color: var(--clr-primary);
  color: var(--clr-white);
  position: absolute;
  top: 1.5rem;
  right: 1.1rem;
  padding: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.user-nav__user-photo {
  height: 3.75rem;
  border-radius: 50%;
  margin-right: 1rem;
}

.side-nav {
  font-size: 1.4rem;
  margin-top: 3.5rem;
  list-style: none;
}
@media only screen and (max-width: 940px) {
  .side-nav {
    display: flex;
    margin-top: 0rem;
  }
}
.side-nav__item {
  position: relative;
}
.side-nav__item:not(:last-child) {
  margin-bottom: 0.5rem;
}
@media only screen and (max-width: 940px) {
  .side-nav__item:not(:last-child) {
    margin-bottom: 0;
  }
}
@media only screen and (max-width: 940px) {
  .side-nav__item {
    flex: 1;
  }
}
.side-nav__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 3px;
  background-color: var(--clr-primary);
  transform: scaleY(0);
  transition: transform 0.2s, width 0.4s cubic-bezier(1, 0, 0, 1) 0.2s, background-color 0.1s;
}
.side-nav__item:hover::before, .side-nav__item--active::before {
  transform: scaleY(1);
  width: 100%;
}
.side-nav__item:active::before {
  background-color: var(--clr-primary-light);
}
.side-nav__link:link, .side-nav__link:visited {
  color: var(--clr-grey-light-1);
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  padding: 1.3rem 3rem;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 940px) {
  .side-nav__link:link, .side-nav__link:visited {
    justify-content: center;
    padding: 2rem;
  }
}
@media only screen and (max-width: 600px) {
  .side-nav__link:link, .side-nav__link:visited {
    flex-direction: column;
    padding: 1.5rem 0.5rem;
  }
}
.side-nav__icon {
  width: 1.75rem;
  height: 1.75rem;
  margin-right: 2rem;
  fill: currentColor;
}
@media only screen and (max-width: 600px) {
  .side-nav__icon {
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0rem;
    margin-bottom: 0.7rem;
  }
}

.legal {
  font-size: 1.2rem;
  color: var(--clr-grey-light-4);
  text-align: center;
  padding: 2.5rem;
}
@media only screen and (max-width: 940px) {
  .legal {
    display: none;
  }
}

.gallerie {
  display: flex;
}
.gallerie__photo {
  width: 100%;
  display: block;
}

.overview {
  display: flex;
  align-items: center;
  border-bottom: var(--line);
}
.overview__heading {
  font-size: 2.25rem;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 2rem 3rem;
}
@media only screen and (max-width: 600px) {
  .overview__heading {
    font-size: 1.8rem;
    padding: 1.25rem 2rem;
  }
}
.overview__stars {
  margin-right: auto;
  display: flex;
}
.overview__icon-star, .overview__icon-location {
  width: 1.75rem;
  height: 1.75rem;
  fill: var(--clr-primary);
}
.overview__location {
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  align-self: stretch;
}
.overview__icon-location {
  margin-right: 0.5rem;
}
.overview__rating {
  background-color: var(--clr-primary);
  color: var(--clr-white);
  margin-left: 3rem;
  padding: 2rem 3rem;
  align-self: stretch;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 600px) {
  .overview__rating {
    margin-left: 2rem;
    padding: 0rem 1.5rem;
  }
}
.overview__rating-average {
  font-size: 2.5rem;
  font-weight: 300;
}
@media only screen and (max-width: 600px) {
  .overview__rating-average {
    font-size: 1.5rem;
  }
}
.overview__rating-count {
  font-size: 0.8rem;
  text-transform: uppercase;
}
@media only screen and (max-width: 600px) {
  .overview__rating-count {
    font-size: 0.5rem;
  }
}

.btn-inline {
  border: none;
  border-bottom: 1px solid currentColor;
  color: var(--clr-primary);
  font-size: inherit;
  display: inline-block;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.2s;
}
.btn-inline span {
  margin-left: 3px;
  transition: margin-left 0.2s;
}
.btn-inline:hover {
  color: var(--clr-grey-dark-1);
}
.btn-inline:hover span {
  margin-left: 8px;
}
.btn-inline:focus {
  outline: none;
  animation: pulstate 1s infinite;
}

@keyframes pulstate {
  0% {
    transform: scale(1);
    box-shadow: none;
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 1rem 4rem rgba(var(--clr-black), 0.25);
  }
  100% {
    transform: scale(1);
    box-shadow: none;
  }
}
.paragraph:not(:last-of-type) {
  margin-bottom: 2rem;
}

.list {
  margin: 3rem 0;
  list-style: none;
  padding: 3rem;
  border-top: var(--line);
  border-bottom: var(--line);
  display: flex;
  flex-wrap: wrap;
}
.list__item {
  flex: 0 0 50%;
  margin-bottom: 0.7rem;
}
.list__item::before {
  content: "";
  display: inline-block;
  height: 1rem;
  width: 1rem;
  margin-right: 0.7rem;
  background-image: url("../img/chevron-thin-right.svg");
  background-size: cover;
}
@supports (-webkit-mask-image: url()) {
  .list__item::before {
    background-color: var(--clr-primary);
    -webkit-mask-image: url("../img/chevron-thin-right.svg");
    -webkit-mask-size: cover;
    mask-image: url("../img/chevron-thin-right.svg");
    mask-size: cover;
    background-image: none;
  }
}

.recommend {
  font-size: 1.3rem;
  color: var(--clr-grey-dark-3);
  display: flex;
  align-items: center;
}
.recommend__count {
  margin-right: auto;
}
.recommend__friends {
  display: flex;
}
.recommend__photo {
  box-sizing: content-box;
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  border: 3px solid var(--clr-white);
}
.recommend__photo:not(:last-child) {
  margin-right: -1.5rem;
}

.review {
  background-color: var(--clr-white);
  box-shadow: var(--shadow-light);
  padding: 3rem;
  margin-bottom: 3.5rem;
  position: relative;
  overflow: hidden;
}
@media only screen and (max-width: 940px) {
  .review {
    padding: 2rem;
    margin-bottom: 3rem;
  }
}
.review__text {
  z-index: 2;
  position: relative;
}
.review__user {
  display: flex;
  align-items: center;
  margin-top: 3rem;
}
.review__photo {
  height: 4.5rem;
  width: 4.5rem;
  border-radius: 50%;
}
.review__user-box {
  margin-left: 1.5rem;
  margin-right: auto;
}
.review__user-name {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 0.4rem;
}
.review__user-date {
  font-size: 1rem;
  color: var(--clr-grey-dark-3);
}
.review__rating {
  font-weight: 300;
  font-size: 2.2rem;
}
.review::before {
  content: "â€œ";
  position: absolute;
  top: -3.54rem;
  left: -1.2rem;
  font-size: 30rem;
  line-height: 1;
  font-family: sans-serif;
  color: var(--clr-grey-light-3);
  z-index: 1;
}

.cta {
  padding: 3.5rem 0;
  text-align: center;
}
@media only screen and (max-width: 940px) {
  .cta {
    padding: 2.5rem 0;
  }
}
.cta__book-now {
  font-size: 1.5rem;
  font-weight: 300;
  text-transform: uppercase;
  margin-bottom: 2.5rem;
}

.btn {
  font-size: 1.5rem;
  font-weight: 300;
  font-family: inherit;
  text-transform: uppercase;
  border-radius: 100px;
  background-image: linear-gradient(to right, var(--clr-primary-light), var(--clr-primary-dark));
  color: var(--clr-white);
  border: none;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}
.btn > * {
  display: inline-block;
  height: 100%;
  width: 100%;
  transition: all 0.4s;
}
.btn__visible {
  padding: 2rem 7.5rem;
}
.btn__invisible {
  position: absolute;
  padding: 2rem 0rem;
  left: 0;
  top: -100%;
}
.btn:hover {
  background-image: linear-gradient(to left, var(--clr-primary-light), var(--clr-primary-dark));
}
.btn:hover .btn__visible {
  transform: translateY(100%);
}
.btn:hover .btn__invisible {
  top: 0;
}
.btn:focus {
  outline: none;
  animation: pulstate 0.6s infinite;
}

.container {
  max-width: 120rem;
  margin: 8rem auto;
  background-color: var(--clr-grey-light-1);
  box-shadow: var(--shadow-dark);
}
@media only screen and (max-width: 1200px) {
  .container {
    margin: 0rem;
    max-width: 100%;
    width: 100%;
  }
}

.header {
  font-size: 1.4rem;
  height: 7rem;
  background-color: var(--clr-white);
  border-bottom: var(--line);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 500px) {
  .header {
    flex-wrap: wrap;
    align-content: space-around;
    height: 11rem;
  }
}

.content {
  display: flex;
}
@media only screen and (max-width: 940px) {
  .content {
    flex-direction: column;
  }
}

.sidebar {
  background-color: var(--clr-grey-dark-1);
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: 18%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hotel-view {
  background-color: var(--clr-white);
  flex: 1;
}

.detail {
  font-size: 1.4rem;
  display: flex;
  padding: 4.5rem;
  background-color: var(--clr-grey-light-1);
  border: var(--line);
}
@media only screen and (max-width: 940px) {
  .detail {
    padding: 3rem;
  }
}
@media only screen and (max-width: 600px) {
  .detail {
    flex-direction: column;
  }
}

.description {
  background-color: var(--clr-white);
  flex: 0 0 60%;
  margin-right: 4.5rem;
  box-shadow: var(--shadow-light);
  padding: 3rem;
}
@media only screen and (max-width: 940px) {
  .description {
    padding: 2rem;
    margin-right: 3rem;
  }
}
@media only screen and (max-width: 600px) {
  .description {
    margin-right: 0;
    margin-bottom: 3rem;
  }
}

.user-reviews {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
